body{margin:0px;background-color:#fff;font-family:"Fredoka One",cursive}.main__container{width:100%;display:flex;flex-direction:row}.payments{text-align:center;display:flex;align-items:center;justify-content:space-around;flex-direction:row;width:100%}.payments input{margin:10px 0px;background-color:#fff;border-left:0px;border-top:0px;border-right:0px;border:1px solid #416D80;border-radius:5px;font-size:14px;font-family:"Fredoka One",cursive;padding:0px 20px;outline:none;height:30px;width:50%}.payments button{background-color:#05C4E6;border-radius:5px;border:none;cursor:pointer;color:white;font-size:14px;font-weight:bold;font-family:'Muli', sans-serif;height:30px;letter-spacing:1px;margin:10px 0px;width:60%}
