body{margin:0px;background-color:#fff;font-family:"Fredoka One",cursive}.main__container{width:100%;display:flex;flex-direction:row}.login input{margin:10px 0px;background-color:#fff;border-left:0px;border-top:0px;border-right:0px;border:1px solid #416D80;border-radius:10px;font-size:16px;font-family:"Fredoka One",cursive;padding:0px 20px;outline:none;height:45px;width:90%}.login__container_social-media--Google-button{background-color:white !important;font-size:12px !important;color:#000 !important;padding:10px}.login__container_social-media--Twitter-button{background-color:#1da1f2 !important;font-size:12px !important}.login__container_social-media--facebook-button{background-color:#5890ff !important;font-size:12px !important}.login button{background-color:#05C4E6;border-radius:10px;border:none;cursor:pointer;color:white;font-size:16px;font-weight:bold;font-family:'Muli', sans-serif;height:50px;letter-spacing:1px;margin:10px 0px;width:100%}.login label{width:40%;display:flex}.login h2{text-align:center}.login{background:#ecedee;display:flex;align-items:center;justify-content:center;flex-direction:column;padding:0px 30px;min-height:calc(100vh - 200px)}.login__container{background-color:rgba(255,255,255,0.2);border:2px solid white;border-radius:40px;color:#416D80;padding:60px 68px 40px;min-height:600px;width:300px;display:flex;justify-content:space-around;flex-direction:column}.login__container--form{display:flex;flex-direction:column}.login__container--form label{font-size:14px}.login__container--remember-me{color:#416D80;display:flex;justify-content:space-between;margin-top:10px}.login__container--remember-me input{margin:-15px;padding:0px;transform:scale(0.4)}.login__container--remember-me a{color:#416D80;font-size:14px;text-decoration:none}.login__container--remember-me a:hover{text-decoration:underline}.login__container_social-media>div{display:flex;align-items:center;font-size:14px;margin-bottom:10px}.login__container_social-media>div>img{width:30px;margin-right:10px}.login__container--register{font-size:14px}.login__container--register a{font-size:16px;color:#416D80;font-weight:bold;text-decoration:none}.login__container--register a:hover{text-decoration:underline}::-moz-placeholder{color:#416D80}:-ms-input-placeholder{color:#416D80}::-ms-input-placeholder{color:#416D80}::placeholder{color:#416D80}@media only screen and (max-width: 600px){.login__container{background-color:transparent;border:none;padding:0px;width:100%}}
