ld(3){transform-origin:0% 100%}.header__burguer__menu input:checked ~ span{opacity:1;transform:rotate(45deg) translate(-2px, -1px);background:#232323}.header__burguer__menu input:checked ~ span:nth-last-child(4){opacity:0;transform:rotate(0deg) scale(0.2, 0.2)}.header__burguer__menu input:checked ~ span:nth-last-child(3){transform:rotate(-45deg) translate(0, -1px)}}
