body{margin:0px;background-color:#fff;font-family:"Fredoka One",cursive}.main__container{width:100%;display:flex;flex-direction:row}@-webkit-keyframes not-found{0%{transform:scale(1)}50%{transform:scale(1.1)}100%{transform:scale(1)}}@keyframes not-found{0%{transform:scale(1)}50%{transform:scale(1.1)}100%{transform:scale(1)}}.animated{-webkit-animation-duration:2.5s;animation-duration:2.5s;-webkit-animation-fill-mode:both;animation-fill-mode:both;-webkit-animation-iteration-count:infinite;animation-iteration-count:infinite}@-webkit-keyframes pulse{0%{transform:scale(1)}50%{transform:scale(1.1)}100%{transform:scale(1)}}@keyframes pulse{0%{transform:scale(1)}50%{transform:scale(1.1)}100%{transform:scale(1)}}.not-found{font-size:100px;text-align:center;color:#416D80;-webkit-animation-name:pulse;animation-name:pulse;-webkit-animation-duration:1s;animation-duration:1s}.not-found__text{font-size:20px;text-align:center;color:#416D80}.not-found__container{background:linear-gradient(#82D9FF, #ecedee);display:flex;align-items:center;justify-content:center;flex-direction:column;padding:0px 30px;min-height:calc(100vh - 200px)}.not-found__footer{background-color:#ecedee;display:flex;align-items:center;height:200px;width:100%}.not-found__footer a{color:#416D80;cursor:pointer;font-size:14px;padding-left:30px;text-decoration:none}.not-found__footer a:hover{text-decoration:underline}
